import mySelect from '../../components/xfl-select/xfl-select.vue';
export default {
	components: { mySelect },
	data() {
		return {
			form: {
				id: '',
				type: '2',
				name: ''
			},
			list: [
				//要展示的数据
				{
					value: '学生',
					id: '1',
				},
				{
					value: '家长',
					id: '2'
				},
				{
					value: '老师',
					id: '3'
				},
				{
					value: '行政管理人员',
					id: '4'
				}
			]
		};
	},
	methods: {
		goSignUp(){
			this.$emit('switchStatus')
		},
		handleSelectChange({
			index,newVal,oldVal,orignItem
		}){
			this.form.type = orignItem.id
		},
		handleConfirm() {
			uni.getUserInfo({
				provider: 'weixin',
				success: res => {
					console.log(res, 'res');
					uni.setStorageSync('userInfo',res)
					uni.setStorageSync('idType',this.form.type)
					uni.navigateBack()
				},
				fail:() =>{
					uni.showToast({
						title:"请同意授权用户信息!",
						icon:'none'
					})
				}
			});
		},
		judgeAuthSetting(){
			uni.getSetting({
				success: info => {
					/*
						{"errMsg":"getSetting:ok","authSetting":{"scope.userInfo":true,"scope.record":true}}
					*/
				   // 用于权限判断，在登录部分进行处理
				   if(info.authSetting['scope.userInfo']){
					   
				   }else{
					   
				   }
				}
			});
		},
	}
};
